import { Customer } from '@core/models/customer.model';
import { DictionaryService } from '@core/services/dictionary/dictionary.service';
import { Component, OnInit, Inject, Input, Output, EventEmitter } from '@angular/core';
import { LABORAL_SITUATION, STREET_TYPES, StreetTypes } from '@core/constants';
import { Cnogroupcnocnae } from '@api/models';
import { Validators } from '@angular/forms';
import { FORM_CASE } from '@core/config/form-case';
import { AddressLocation } from '@api/models/addressLocation';
import {
  emailRegExp,
  spanishPhoneRegExp,
  initPostalCodeAndLocationRegExp,
  streetTypeNameAndNumberFloorDoor,
} from '@shared/utils';
import { noWhiteSpaceValidator } from '@shared/validators';

export enum CUSTOMER_CASE_FORM {
  TYPE_CASE = 'typeCase',
  CATEGORY = 'category',
  COMENTS = 'coments',
  EMAIL = 'email',
  PHONE = 'phone',
  STREET_TYPE = 'streetType',
  STREET_NAME = 'streetName',
  STREET_NUMBER = 'streetNumber',
  PORTAL = 'portal',
  FLOOR = 'floor',
  DOOR = 'door',
  POSTAL_CODE = 'postalCode',
  LOCATION_CODE = 'locationCode',
  ADDRESS = 'address',
  ADDRESS_LOCATION = 'addressLocation',
  EMPLOYMENT_SITUATION = 'employmentSituation',
}
@Component({
  selector: 'app-info-personal-data',
  templateUrl: './info-personal-data.component.html',
  styleUrls: ['./info-personal-data.component.scss'],
})
export class InfoPersonalDataComponent implements OnInit {
  @Input() formControl;
  @Input() customer: Customer;
  @Output() updateChange = new EventEmitter<any>();
  @Output() formError = new EventEmitter<boolean>();
  @Output() changePassword = new EventEmitter<void>();

  formCase = FORM_CASE;
  employmentSituation: Cnogroupcnocnae[] = [];
  locationList: AddressLocation[] = [];
  updatePersonalDate = new Object();

  emailValidator = [Validators.required, Validators.pattern(emailRegExp), noWhiteSpaceValidator];
  phoneValidator = [
    Validators.required,
    Validators.pattern(spanishPhoneRegExp),
    noWhiteSpaceValidator,
  ];
  addressValidator = [Validators.required, Validators.pattern(streetTypeNameAndNumberFloorDoor)];
  postalCodeAndLocationValidation = [
    Validators.required,
    Validators.pattern(initPostalCodeAndLocationRegExp),
  ];
  validationRequired = [Validators.required];
  postalCodeEmit: string;
  addressConcat: string;
  postalCodeAndLocation: string;

  constructor(
    private dictionaryService: DictionaryService,
    @Inject(STREET_TYPES) public streetTypes: StreetTypes
  ) {}

  ngOnInit(): void {
    if (this.customer) {
      this.setLocationStream(this.customer.postalCode);
      this.addressConcat = this.concatAddress();
      this.concatPostalCodeAndLocation(this.locationList);
      this.dictionaryService.getEmploymentStatus().subscribe((result) => {
        this.employmentSituation = result;
      });
    }
  }

  private concatAddress() {
    const streetType = this.streetTypes.find((opt) => opt.code === this.customer.streetTypes);
    return `${streetType.name}, ${this.customer.streetName}, ${this.customer.streetNumber}`;
  }

  private setLocationStream(value) {
    this.dictionaryService.getLocation(value).subscribe((result) => {
      this.locationList = result;
      this.concatPostalCodeAndLocation(this.locationList);
    });
  }
  private concatPostalCodeAndLocation(locationList) {
    this.postalCodeAndLocation = `${this.customer.postalCode}, ${this.customer.locationName}, ${this.customer.provinceName}`;
  }

  getOptionsNameLaboralSituation(laboralSituationCode) {
    return this.employmentSituation.find((ls) => ls.code === laboralSituationCode)?.name;
  }

  saveEditable(value, param) {
    switch (param) {
      case CUSTOMER_CASE_FORM.ADDRESS:
        if (this.addressConcat !== value) {
          this.updatePersonalDate[param] = value;
        } else {
          if (this.updatePersonalDate.hasOwnProperty(param)) {
            delete this.updatePersonalDate[param];
          }
        }
        break;

      case CUSTOMER_CASE_FORM.ADDRESS_LOCATION:
        if (this.postalCodeAndLocation !== value) {
          this.updatePersonalDate[param] = value;
        } else {
          if (this.updatePersonalDate.hasOwnProperty(param)) {
            delete this.updatePersonalDate[param];
          }
        }
        break;

      case CUSTOMER_CASE_FORM.EMPLOYMENT_SITUATION:
        if (this.postalCodeAndLocation !== value) {
          this.updatePersonalDate[param] = this.employmentSituation.find(
            (lab) => lab.code === value
          ).name;
        } else {
          if (this.updatePersonalDate.hasOwnProperty(param)) {
            delete this.updatePersonalDate[param];
          }
        }
        break;

      default:
        if (this.customer[param] !== value) {
          this.updatePersonalDate[param] = value;
        } else {
          if (this.updatePersonalDate.hasOwnProperty(param)) {
            delete this.updatePersonalDate[param];
          }
        }
        break;
    }
    this.updateChange.emit(this.updatePersonalDate);
  }
}
