import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ReGenerationPswData } from '@api/models/reGenerationPswData';
import { environment } from '@environment/environment';

@Injectable({
  providedIn: 'root',
})
export class PasswordService {
  constructor(private http: HttpClient) {}

  changePassword(body: ReGenerationPswData) {
    const url = `${environment.apiBasePath}${environment.endpoints.passwordRegeneration}`;
    return this.http.post<{ otpId: string }>(url, body);
  }

  confirmChangePassword(body: ReGenerationPswData) {
    const url = `${environment.apiBasePath}${environment.endpoints.passwordRegeneration}`;
    return this.http.put(url, body);
  }
}
