import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, AbstractControl, Validators } from '@angular/forms';
import { passwordFormat, matchValidator } from '@core/utils';

@Component({
  selector: 'app-change-password-form',
  templateUrl: './change-password-form.component.html',
  styles: [],
})
export class ChangePasswordFormComponent implements OnInit {
  @Output() changePassword = new EventEmitter<ChangePasswordData>();
  @Output() hide = new EventEmitter<void>();
  form: FormGroup;
  submitted = false;

  constructor(private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.createForm();
  }

  onSubmit() {
    this.submitted = true;
    this.form.markAllAsTouched();

    if (this.form.valid) {
      this.changePassword.emit({
        password: this.form.value.password,
        newPassword: this.form.value.newPassword,
      });
    }
  }

  hasError(control: AbstractControl) {
    return control.touched && control.invalid;
  }

  hasMismatchError() {
    return this.form.get('newPasswordConfirm').touched && this.form.hasError('mismatch');
  }

  private createForm() {
    this.form = this.formBuilder.group(
      {
        password: ['', [Validators.required]],
        newPassword: ['', [Validators.required, Validators.pattern(passwordFormat)]],
        newPasswordConfirm: ['', [Validators.required, Validators.pattern(passwordFormat)]],
      },
      { validators: matchValidator('newPassword', 'newPasswordConfirm') }
    );
  }
}

export interface ChangePasswordData {
  password: string;
  newPassword: string;
}
