import { Customer } from '@core/models/customer.model';
import { AccountService, createCustomerMap } from '@core/services/account/account.service';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { HeaderService } from '@core/services/header/header.service';
import { filter } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ChangePasswordData } from './components/change-password-form/change-password-form.component';
import { PasswordService } from '@core/services/password/password.service';
import { Subscription } from 'rxjs';
import { encrypt, PUBLIC_KEY, ALGORITHM, isInvalidOtp } from '@core/utils';
import { ErrorHandlerService } from '@core/services/errors/error-handler.service';
import { ReGenerationPswData } from '@api/models/reGenerationPswData';

@Component({
  selector: 'app-personal-data',
  templateUrl: './personal-data.component.html',
  styles: [],
})
export class PersonalDataComponent implements OnInit {
  @ViewChild('changePasswordModal') changePasswordModal: TemplateRef<any>;
  @ViewChild('otpModal') otpModal: TemplateRef<any>;
  @ViewChild('successChangePasswordModal') successChangePasswordModal: TemplateRef<any>;

  readonly customer$ = this.getCustomer();
  invalidOtp = false;

  private changePasswordOtpId: string;
  private changePasswordData: ReGenerationPswData;

  private modalRef: BsModalRef;
  private subs = new Subscription();

  constructor(
    private headerService: HeaderService,
    private accountService: AccountService,
    private modalService: BsModalService,
    private passwordService: PasswordService,
    private errorHandler: ErrorHandlerService
  ) {}

  ngOnInit() {
    this.headerService.updateTitle('menu_profile');
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template);
  }

  closeModal() {
    if (this.modalRef) {
      this.modalRef.hide();
    }
  }

  getUsername(customer: Customer) {
    return `${customer.name} ${customer?.firstSurname} ${customer.secondarySurname}`;
  }

  onClickChangePassword() {
    this.openModal(this.changePasswordModal);
  }

  async onChangePassword(passwordData?: ChangePasswordData) {
    const password = await encrypt(passwordData?.password, PUBLIC_KEY, ALGORITHM);
    const newPassword = await encrypt(passwordData?.newPassword, PUBLIC_KEY, ALGORITHM);

    this.changePasswordData = {
      password,
      newPpassword: newPassword,
      confirmNewPassword: newPassword,
    };

    this.closeModal();
    this.generateOtp();
  }

  generateOtp() {
    this.invalidOtp = false;
    this.subs.add(
      this.passwordService.changePassword(this.changePasswordData).subscribe(
        ({ otpId }) => {
          this.changePasswordOtpId = otpId;
          this.openModal(this.otpModal);
        },
        (error) => {
          this.closeModal();
          this.errorHandler.showError(error);
        }
      )
    );
  }

  onSendOtp(secretKey: string) {
    this.invalidOtp = false;
    this.subs.add(
      this.passwordService
        .confirmChangePassword({
          ...this.changePasswordData,
          otp: { secretKey, reference: this.changePasswordOtpId },
        })
        .subscribe(
          () => {
            this.closeModal();
            this.openModal(this.successChangePasswordModal);
          },
          (error) => {
            if (isInvalidOtp(error)) {
              this.invalidOtp = true;
              return;
            }

            this.closeModal();
            this.errorHandler.showError(error);
          }
        )
    );
  }

  private getCustomer() {
    return this.accountService.currentCustomer$.pipe(
      filter((customer) => !!customer),
      createCustomerMap()
    );
  }
}
